@import 'var/color';
@import 'var/line';
@import 'var/med_query';

// Component
@import'style_comp/navbar';
@import'style_comp/home';
@import'style_comp/portofolio';
@import'style_comp/me';

body{
	overflow-x: hidden;
	background-color: $color1;
  background-image: 
    linear-gradient(90deg, transparent, transparent $one-third0, $line-color $one-third1, transparent $one-third1),
    linear-gradient(90deg, transparent, transparent $two-third1, $line-color $two-third0, transparent $two-third0);
//   linear-gradient(90deg, transparent, transparent $tre-third1, $line-color $tre-third0, transparent $tre-third0),
//     linear-gradient(90deg, transparent, transparent $for-third1, $line-color $for-third0, transparent $for-third0),
//   linear-gradient(90deg, transparent, transparent $fiv-third1, $line-color $fiv-third0, transparent $fiv-third0),
//   linear-gradient(90deg, transparent, transparent $six-third1, $line-color $six-third0, transparent $six-third0);
//   linear-gradient(90deg, transparent, transparent $sev-third1, $line-color $sev-third0, transparent $sev-third0);
  transform: translate3d(0, 0, 0);
}

* {
    margin: 0;
    padding: 0;
    font-family: 'Rubik', sans-serif;
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}
